html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
}
body.fijar-body {
  overflow: hidden;
  padding-right: 2rem;
}
body.body-scroll {
  padding-top: 109px;
}

p {
  font-family: "Krub", sans-serif;
  font-size: 1.8rem;
  color: #0a070b;
  line-height: 1.3;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0% auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  text-shadow: -2px 2px 0px #0a070b;
  font-family: "Bangers", cursive;
  letter-spacing: 5px;
  margin-bottom: 1rem;
  font-weight: 100;
}

h3 {
  font-size: 3rem;
}

li, ul, ol {
  font-family: "Krub", sans-serif;
  font-weight: 100;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.label {
  background-color: #fffeec;
  font-family: "Bungee Hairline", cursive;
  font-weight: 600;
  border: 0.5px solid #0a070b;
  box-shadow: -6px 8px 1px #0a070b;
  display: inline-block;
  padding: 0.5rem 1rem;
  font-size: 2rem;
}

.marco {
  border: 5px solid #0bf856;
  border-bottom: 25px solid #0bf856;
  box-shadow: -12px 16px 0px #0a070b;
}

.header {
  z-index: 1;
  background-color: white;
  box-shadow: -6px 8px 1px #0a070b;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: -6px 8px 1px #0a070b;
  }
}
.header h1 {
  font-size: 6rem;
  color: #fa61ba;
  text-shadow: -2px 2px 0px #0a070b;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header spam {
  font-size: 5rem;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion-principal {
  font-family: "Bungee Hairline", cursive;
  font-weight: 600;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 3rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  background-color: #0bf856;
  box-shadow: -8px 10px 1.8px #0a070b;
}
.navegacion-principal a:hover:active {
  color: #fffeec;
  box-shadow: -6px 8px 1px #0a070b;
}

.video {
  z-index: -1;
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(4, 255, 0);
  background: linear-gradient(270deg, rgba(4, 255, 0, 0.15) 0%, rgba(255, 0, 140, 0.15) 68%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.video video {
  width: auto;
  display: block;
  border: 5px;
}
@media (min-width: 768px) {
  .video video {
    width: 100;
  }
}

.contenido-video h2 {
  transform: rotate(-3deg);
  color: #fffeec;
  font-size: 7rem;
  margin-bottom: 3rem;
}
.contenido-video span {
  color: #0bf856;
  font-size: 9rem;
  font-family: "Rock Salt", cursive;
  text-shadow: -6px 6px 0px #0a070b;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  color: #fa61ba;
}

.lineup {
  background-color: #036eb9;
  padding: 5rem 0;
}
.lineup h2, .lineup p {
  text-align: center;
  color: #fffeec;
}
.lineup h2 {
  transform: rotate(-3deg);
  color: #fffeec;
  font-size: 6rem;
  margin-bottom: 1rem;
}
.lineup span {
  color: #0bf856;
  font-size: 5rem;
  font-family: "Rock Salt", cursive;
  text-shadow: -6px 6px 0px #0a070b;
}
.lineup .dia-contenedor {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1.5rem 0 2rem 0;
}
.lineup .dia {
  color: #0a070b;
}

@media (min-width: 768px) {
  .escenario-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenario-contenedor p {
  color: #0a070b;
}

.escenario {
  padding-top: 2rem;
}
.escenario .nombre-escenario::after {
  content: "";
  display: block;
  width: 20rem;
  height: 5rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 1.5rem auto 0 auto;
}

.metallica .nombre-escenario::after {
  background-image: url(/build/img/metallica.avif);
}

.oasis .nombre-escenario::after {
  background-image: url(/build/img/oasis.avif);
}

.acdc .nombre-escenario::after {
  background-image: url(/build/img/acdc.avif);
}

.soad .nombre-escenario::after {
  background-image: url(/build/img/soad.avif);
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 1rem;
}
.calendario li span {
  color: #0a070b;
  font-weight: 600;
  font-size: 2.5rem;
  padding-left: 2.5rem;
  font-family: "Bungee Hairline", cursive;
  text-shadow: -1px 1px 0px #fffeec;
  letter-spacing: 3px;
}

.nombre-escenario {
  font-size: 4rem;
  text-shadow: -2px 2px 0px #0a070b;
  font-family: "Bangers", cursive;
  letter-spacing: 5px;
  margin: 1rem;
  font-weight: 100;
}

.bg-color1 {
  background-color: #fa61ba;
  color: #fffeec;
}
.bg-color1 li:nth-child(odd) {
  background-color: #fa52b4;
}
.bg-color1 .nombre-escenario {
  color: #f5d797;
}

.bg-color2 {
  background-color: #f5d797;
}
.bg-color2 li:nth-child(odd) {
  background-color: #f3d084;
}
.bg-color2 .nombre-escenario {
  color: #fa61ba;
}

.galeria {
  padding: 5rem 0;
}
.galeria h2 {
  text-align: center;
  transform: rotate(-3deg);
  color: #fa61ba;
  font-size: 6rem;
  margin-bottom: 3rem;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.6);
}
.overlay picture {
  display: block;
  max-width: 75rem;
  border: 0.5rem solid #fffeec;
  border-bottom: 4rem solid #fffeec;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  background-color: #fa61ba;
  color: #0a070b;
  display: grid;
  place-items: center;
  font-weight: 600;
  border: 1px solid #fffeec;
  box-shadow: -3px 3px 0px #fffeec;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  background-color: #036eb9;
  padding: 5rem 0;
}
.boletos h2 {
  text-align: center;
  transform: rotate(-3deg);
  color: #fa61ba;
  font-size: 6rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase .nombre-pase {
  font-family: "Bangers", cursive;
  color: #fffeec;
  font-weight: 100;
  font-size: 3.5rem;
  letter-spacing: 5px;
  text-align: center;
  text-shadow: -2px 2px 0px #0a070b;
  letter-spacing: 5px;
  margin: 0;
  margin-bottom: 2rem;
  transform: rotate(-6deg);
}
.pase .nombre-pase span {
  color: #0bf856;
  font-size: 4rem;
  font-family: "Rock Salt", cursive;
  text-shadow: -6px 6px 0px #0a070b;
}
.pase .sombra {
  border: 0.5px solid #0a070b;
  box-shadow: -6px 8px 1px #0a070b;
}
.pase-header p, .pase-footer p {
  text-align: center;
  margin: 0;
}
.pase .label {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 6px;
  color: #0a070b;
  background-color: #f5d797;
  box-shadow: 0px 6px 1px #0a070b;
}
.pase-header {
  background-color: #fffeec;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #fffeec;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #fffeec;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #fa61ba;
}
.pase-footer p {
  color: #fffeec;
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
}